.list {
  margin: 0;
  padding: 0;
}

.item {
  font-family: Roboto;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.2;
  letter-spacing: 0.4px;

  margin-bottom: 8px;
}

.itemLink {
  text-decoration: none;
  color: #2e2f42;
}

.itemLink:hover {
  color: #f65b18;
  font-weight: 600;
}
